<template>
    <div>
        <!-- 顶部操作 -->
        <div class="baseTop">
            <el-button type="primary" @click="addUser">添加</el-button>
        </div>
       <!-- 列表数据 -->
        <el-table
            border
            :data="userList"
            style="width: 100%">
            <el-table-column
                prop="loginName"
                label="登录账号">
            </el-table-column>
             <el-table-column
                prop="userName"
                label="用户名">
            </el-table-column>
             <el-table-column
                prop="email"
                label="秘邮邮箱">
            </el-table-column>
            <el-table-column
                prop="remark"
                label="用户描述"
                width="180">
            </el-table-column>
            <el-table-column 
                            label="操作"
                            width="400"
                            align='center'>
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        type="primary"
                        @click="editUser(scope.row)">编辑</el-button>
                        <el-button
                        size="mini"
                        type="warning"
                        @click="resetPwd(scope.row)">重置用户密码</el-button>
                    <el-button
                        size="mini"
                        @click="check(scope.row)">查看用户角色</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 添加、编辑弹框 -->
        <el-dialog
            :title="!isAdd ? '添加' : '编辑'"
            :visible.sync="isShowAddDialog"
            width="50%"
            :before-close="cancel">
            <div class="addItem" v-if="isAdd">
                <span>用户Id</span>
                <el-input
                    placeholder="请输入用户Id"
                    v-model="userId"
                    class="itemInput"
                    :disabled="isAdd">
                </el-input>
            </div>
            <div class="addItem">
                <span>登录账号</span>
                <el-input
                    placeholder="请输入登录账号"
                    v-model="loginName"
                    class="itemInput">
                </el-input>
            </div>
            <div class="addItem">
                <span>用户名</span>
                <el-input
                    placeholder="请输入用户名"
                    v-model="userName"
                    class="itemInput">
                </el-input>
            </div>
            <div class="addItem">
                <span>秘邮邮箱</span>
                <el-input
                    placeholder="请输入邮箱"
                    v-model="email"
                    class="itemInput">
                </el-input>
            </div>
            <div class="addItem">
                <span>用户描述</span>
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="请输入内容"
                    v-model="remark"
                    class="itemInput">
                </el-input>
            </div>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="confirm">{{isAdd ? '编 辑' : '添 加'}}</el-button>
            </span>
        </el-dialog>
        <!-- 删除弹框 -->
        <el-dialog
            title="删除"
            :visible.sync="isShowDeleteDialog"
            width="30%"
            :before-close="cancel">
            <span v-if="itemData">是否确认删除<span class="tips">{{itemData.userName}}</span>用户</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="danger" @click="deleteUser">删 除</el-button>
            </span>
        </el-dialog>
        <page :total='total' :pageNo='postData.pageNo' @togglePages='togglePages'></page>
    </div>
</template>

<script>
import { get_user_list_api, get_user_resetPwd_api, get_user_save_api, get_user_delete_api } from '@/api/system';
import page from '@/components/page';
export default {
    data() {
        return {
            email: '',
            loginName: '',
            remark: '',
            userId: '',
            userName: '',
            isAdd:false,
            isShowDeleteDialog: false, // 是否显示删除框
            isShowAddDialog: false, // 是否显示删除框
            postData: {
                pageNo: 1,
                pageSize: 10
            },
            itemData: null, //当前条数据
            userList: [], // 用户列表
            total: '' // 总条数
        }
    },
    methods: {
        // 获取数据
        getData () {
            get_user_list_api(this.postData).then(res => {
                this.userList = res.list;
                this.total = res.total;
            })
        },
        // 校验
        verify () {
            if (this.loginName == '') {
                this.$message({
                    message: '请输入用户登录账号',
                    type: 'warning'
                });
                return
            }
            if (this.userName == '') {
                this.$message({
                    message: '请输入用户名称',
                    type: 'warning'
                });
                return
            }
            if (this.email == '') {
                this.$message({
                    message: '请输入邮箱',
                    type: 'warning'
                });
                return
            }
            // if (this.remark == '') {
            //     this.$message({
            //         message: '请输入用户描述',
            //         type: 'warning'
            //     });
            //     return
            // }
            let obj = {
                loginName: this.loginName,
                email: this.email,
                remark: this.remark,
                userName: this.userName,
            }
            // 如果是编辑就返回该字段
            if( this.isAdd ) {
                obj.userId = this.userId;
            }
            return obj;
        },
        // 添加用户
        addUser () {
            this.isShowAddDialog = true;
            this.isAdd = false;
        },
        // 编辑
        editUser (row) {
            this.userId = row.userId; 
            this.loginName = row.loginName; 
            this.userName = row.userName;
            this.email = row.email;
            this.remark = row.remark;
            this.isAdd = true;
            this.isShowAddDialog = true;
        },
        // 删除
        handleDelete (row) {
            this.itemData = row;
            this.isShowDeleteDialog = true;
        },
        // 查看
        check(row) {
            this.$router.push({
                path: `/userRole/${row.userId}`
            })
        },
        // 取消
        cancel() {
            this.isShowDeleteDialog = false;
            this.isShowAddDialog = false;
        },
        // 分页
        togglePages (page) {
            this.postData.pageNo = page;
            this.getData();
        },
        // 确认添加与编辑
        confirm() {
            let obj = this.verify();
            if( obj ) {
                get_user_save_api(obj).then(() => {
                    if( this.isAdd ) {
                        this.formatData('编辑成功','success');
                    } else {
                        this.formatData('添加成功','success');
                    }
                    this.getData();
                })
            }
        },
        // 确认删除
        deleteUser () {
            get_user_delete_api({userId: this.itemData.userId}).then(() => {
                this.formatData('删除成功','success');
                this.getData();
            })
        },
        // 重置密码
        resetPwd (row) {
            get_user_resetPwd_api({userId: row.userId}).then(() => {
                this.$message({
                    message: "重置密码成功",
                    type: 'success'
                })
            })
        },
        formatData (msg,type) {
            this.postData =  {
                pageNo: 1,
                pageSize: 10
            }
            this.$message({
                message: msg,
                type: type
            });
            this.isShowDeleteDialog = false;
            this.isShowAddDialog = false;
        }
    },
    created() {
        this.getData();
    },
    components: {
        page
    }
}
</script>

<style scoped lang='scss'>
.addItem{
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    span{
        display: block;
        width:150px;
    }
}
</style>